<template>
  <div>
    <Layout-box one="直播间添加商品">
      <el-form :model="addForm" size="small">
        <el-form-item label="选择商品：">
          <el-select v-model="addForm.produId" filterable placeholder="请选择" @change="onSelectGoods">
            <el-option
              v-for="item in goodsList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="addForm.goodsData.skus.length" label="规格：">
          <el-table :data="addForm.goodsData.skus">
            <el-table-column v-for="(item, i) in addForm.goodsData.items" :key="i" :label="item.keyName">
              <template slot-scope="scope">
                <el-input v-model="scope.row.attrSymbolNo" />
              </template>
            </el-table-column>
            <el-table-column label="原价">
              <template slot-scope="scope">
                <el-input v-model="scope.row.originalPrice" />
              </template>
            </el-table-column>
            <el-table-column label="现价">
              <template slot-scope="scope">
                <el-input v-model="scope.row.price" />
              </template>
            </el-table-column>
            <el-table-column label="库存">
              <template slot-scope="scope">
                <el-input v-model="scope.row.stock" />
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="onDelete(scope)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </Layout-box>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'

export default {
  name: 'liveRoomAddGoods',

  components: {
    LayoutBox
  },

  data () {
    return {
      goodsList: [],
      addForm: {
        produId: '',
        goodsData: {
          skus: []
        },
      },
    }
  },

  async created () {
    await this.apiProductQuery()
  },

  methods: {
    // 选择商品
    async onSelectGoods (e) {
      await this.apiProductFindById(e)
    },
    // 删除规格
    onDelete (scope) {
      this.addForm.goodsData.skus.splice(scope.$index, 1)
    },
    // 点击提交
    onSubmit () {
      const data = {
        itemSkus: this.addForm.goodsData.skus,
        produId: this.addForm.goodsData.id
      }
      this.apiInsertAnchorProduct(data)
    },
    
    // -----------------------------------------------
    // 查询商品列表
    async apiProductQuery () {
      const res = await this.$http.get(this.$api.queryAnchorShopProduct)
      this.goodsList = res.data.data
    },
    // 单查商品详情
    async apiProductFindById (id) {
      const res = await this.$http.get(`${this.$api.productFindById}?id=${id}&type=${2}`)
      this.addForm.goodsData = res.data
    },
    // 添加直播商品
    async apiInsertAnchorProduct (data) {
      const res = await this.$http.put(this.$api.insertAnchorProduct, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.$router.go(-1)
      } else {
        this.$message.error(res.data.msg)
      }
    }
  }, 

}
</script>

<style lang="scss" scoped>

</style>
